<template>
    <div class="Carousel-Wrapper">
        <div class="swiper text-cl">
            <!-- Additional required wrapper -->
            <div class="swiper-wrapper">
                <!-- Slides -->
                <div class="swiper-slide swiper-slide1" ref="text1">
                    <img src="https://picsum.photos/id/453/1920/800" alt="">
                    <div class="text">
                        <h1>slide</h1>
                        <h2>subtitle</h2>
                        <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Deleniti libero, laboriosam non veniam quos tenetur tempore accusantium hic sit quidem numquam sed distinctio in, velit exercitationem incidunt eveniet beatae facere.</p>
                        <button>Learn more</button>
                    </div>
                </div>
                <div class="swiper-slide swiper-slide2">
                    <img src="https://picsum.photos/id/452/1920/800" alt="">
                    <div class="text">
                        <h1>slide</h1>
                        <h2>subtitle</h2>
                        <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Deleniti libero, laboriosam non veniam quos tenetur tempore accusantium hic sit quidem numquam sed distinctio in, velit exercitationem incidunt eveniet beatae facere.</p>
                        <button>Learn more</button>
                    </div>
                </div>
                <div class="swiper-slide swiper-slide3">
                    <img src="https://picsum.photos/id/777/1920/800" alt="">
                    <div class="text">
                        <h1>slide</h1>
                        <h2>subtitle</h2>
                        <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Deleniti libero, laboriosam non veniam quos tenetur tempore accusantium hic sit quidem numquam sed distinctio in, velit exercitationem incidunt eveniet beatae facere.</p>
                        <button>Learn more</button>
                    </div>
                </div>
            </div>
            <div class="swiper-pagination"></div>
        </div>
    </div>
</template>

<script>
import Swiper,{ Navigation, Pagination } from 'swiper';

export default {
    name:'text-cl',
    mounted(){
        const swiper = new Swiper('.text-cl', {
            modules: [Navigation, Pagination],
            direction: 'horizontal',
            loop: true,
            effect:'slide',
            grabCursor:true,

            pagination:{
                el:'.swiper-pagination',
                bulletClass:'swiper-pagination-bullet',
                bulletActiveClass:'swiper-pagination-bullet-active',
                clickable:true
            },
        });
        swiper.on('slideChange', function () {
            this.slides[this.activeIndex].classList.add('textAnimation');

            if(this.activeIndex == 0){
                this.slides[this.slides.length-2].classList.add('textAnimation');
            }else if(this.activeIndex == (this.slides.length-1)){
                this.slides[1].classList.add('textAnimation');
            }

            this.slides[this.previousIndex].classList.remove('textAnimation');
        });
        
        this.$bus.$on('text-clAnimation',this.swiperAnimation);

        setTimeout(() => {
            this.swiperAnimation();
        }, 500);
    },
    methods: {
        swiperAnimation() {
            let dom = this.$refs.text1;
            let text = dom.className.indexOf('textAnimation') != -1;
            if (!text) {
                if(dom.getBoundingClientRect().top+30 < document.documentElement.clientHeight && dom.getBoundingClientRect().bottom+30 > 0) {
                    setTimeout(()=>{
                        dom.classList.add('textAnimation');
                    },);  
                }
            }
        },
    },
    beforeDestroy() {
        this.$bus.$off('text-clAnimation');
    },
}
</script>

<style lang="less" scoped>
    // 992以上
    .text-cl{
        max-width: 1920px;
        height: 800px;
        margin: 0 auto;


        .swiper-slide{
            display: flex;
            justify-content: center;
            overflow: hidden;
            position: relative;
            

            img{
                height: 100%;
                vertical-align: middle;
            }

            .text{
                width: 80vw;
                max-width: 1200px;
                color: white;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);

                h1{
                    font-size: 48px;
                    font-weight: 900;
                    margin-bottom: 10px;
                    display: inline-block;
                    border-bottom: solid 2px white;
                }
                h2{
                    font-size: 32px;
                    font-weight: 600;
                    margin-bottom: 5px;
                }
                p{
                    max-width: 600px;
                    font-size: 20px;
                    font-weight: 500;
                    line-height: 1.3em;
                    margin-bottom: 25px;
                }
                button{
                    width: 100px;
                    height: 48px;
                    font-weight: 600;
                    font-size: 14px;
                    color: white;
                    background-color: rgba(0, 0, 0, 0);
                    border: 2px white solid;
                }

                h1,h2,p,button{
                    opacity: 0;
                    transform:translateY(-30px);
                }
            }      
        }
        .swiper-slide.textAnimation{
            .text{
                h1,h2,p,button{
                    opacity: 1;
                    transform:translateY(0px);
                    transition: all 1.5s;
                }
            }    
        }
    }  
</style>

<style lang="less" scoped>
    // 992~768
    @media (max-width:992px)and(min-width:768px) {
        .text-cl{
