<template>
    <div class="wrapper" @scroll="scrollevent">
        <Top></Top>
        <Home :delaytime="delaytime"></Home>
        <Mainfooter></Mainfooter>
    </div>
</template>

<script>
import Top from './top';
import Home from './home';
import Mainfooter from './mainfooter';

export default {
    name:'project',
    components: {Top,Home,Mainfooter},
    data() {
        return {
        isRun:true,
        timeMultiple:0,
        delaytime:500,
        }
    },
    mounted() {
        window.addEventListener('resize',this.scrollevent);
        console.log(456);
    },
    methods: {
        scrollevent(){
        if(this.isRun){
            this.isRun = false;
            this.$bus.$emit('text-clAnimation');
            this.$bus.$emit('homeAnimation',this.delaytime);
            this.isRun = true;
        }
        },

    },
}
</script>

<style lang="less" scoped>
.wrapper{
    &::-webkit-scrollbar {
      width: 10px;
    }

    &::-webkit-scrollbar-thumb {
        border-radius: 50px;
        background-color: rgb(150, 150, 150);
        border: 1px solid white;
    }
}
</style>