<script>
import TextCarousel from "@/components/swiper/text-cl.vue";
import EnlargeCarousel from "@/components/swiper/enlarge-cl.vue";


export default {
  name: "home",
  props:['delaytime'],
  components: { TextCarousel, EnlargeCarousel},
  data() {
    return {
      rowcheck:true,
      introductcheck:true,
      servicetcheck:true,
    }
  },
  computed:{
    rows(){
      return document.querySelectorAll('.row');
    },
    introducts(){
      return document.querySelectorAll('.introduct');
    },
    services(){
      return document.querySelectorAll('.service');
    },
  },
  mounted() {
    this.$bus.$on("homeAnimation", this.scrollAnimation);
    setTimeout(() => {
      this.scrollAnimation(this.delaytime);
    }, 500);
  },
  methods: {
    scrollAnimation(delaytime) {
      let{rows,introducts,services,addClassAnimation,addDomPass} = this;

      addDomPass(rows,0);
      addDomPass(introducts,50)
      addDomPass(services,50);

      if(this.rowcheck){
        addClassAnimation(rows,'rowAnimation',delaytime,'rowcheck',0);
      }

      if(this.introductcheck){
        addClassAnimation(introducts,'introductionAnimation',delaytime,'introductcheck',50);
      }

      if(this.servicetcheck){
        addClassAnimation(services,'serviceAnimation',delaytime,'servicetcheck',50);
      }

    },

    addClassAnimation(doms,classname,delaytime,check,displacement){
      let multiple = 0;

      doms.forEach(dom=>{
        if(!dom.check){
          if((dom.getBoundingClientRect().top+displacement+30) < document.documentElement.clientHeight && (dom.getBoundingClientRect().bottom+displacement) >= 0){
            dom.check = true;
            setTimeout(()=>{
              dom.classList.add(classname);
            },multiple*delaytime);

            multiple++;
          }
        }
      });
      
      this[check] = false;

      setTimeout(()=>{
        this[check] = true;
      },multiple*delaytime);

    },

    addDomPass(doms,displacement){
      doms.forEach(dom => {
        if(!dom.pass){
          if((dom.getBoundingClientRect().top+displacement) < document.documentElement.clientHeight && (dom.getBoundingClientRect().bottom+displacement) > 0){
            dom.pass = true;
          }
        }
      });
    }

  },
  watch:{
    rowcheck:{
      handler(newValue,oldValue){
       
        if(newValue){
          let multiple = 0;
          this.rowcheck = false;

          this.rows.forEach(dom=>{
            if(dom.pass&&!dom.check){
             
             dom.check = true;
              setTimeout(()=>{
                dom.classList.add('rowAnimation');
              },multiple*this.delaytime);

              multiple++;
            }
          });

          setTimeout(()=>{
            this.rowcheck = true;
          },multiple*this.delaytime);
        }
      }
    },
    introductcheck:{
      handler(newValue,oldValue){
        
        if(newValue){
          let multiple = 0;
          this.introductcheck = false;

          this.introducts.forEach(dom=>{
            if(dom.pass&&!dom.check){
              dom.check = true;

              setTimeout(()=>{
                dom.classList.add('introductionAnimation');
              },multiple*this.delaytime);

              multiple++;
            }
          });

          setTimeout(()=>{
            this.introductcheck = true;
          },multiple*this.delaytime);
        }
      }
    },
    servicetcheck:{
      handler(newValue,oldValue){

        if(newValue){
          let multiple = 0;
          this.servicetcheck = false;

          this.services.forEach(dom=>{
            if(dom.pass&&!dom.check){
              
              dom.check = true;
              setTimeout(()=>{
                dom.classList.add('serviceAnimation');
              },multiple*this.delaytime);

              multiple++;
            }
          });

          setTimeout(()=>{
            this.servicetcheck = true;
          },multiple*this.delaytime);
        }
      }
      
    },
  },
  beforeDestroy() {
    this.$bus.$off("scrollAnimation");
  },
};
</script>

