                        background-color: black;
                        transform-origin: right center;
                        transition:transform 1s;
                    }

                    & > li:nth-child(2){
                        margin: 5px 0;
                    }
                }

                .menu-list{
                    height: 0px;
                    overflow: hidden;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    background-color: white;
                    position: absolute;
                    z-index: 99;
                    top: 40px;
                    right: 0;
                    left: 0px;
                    margin-right: 0px;
