                        }
                        

                        a{
                            padding: 0;
                            border-radius: 0;
                            text-align: center;
                            font-size: 20px;
                            line-height: 50px;
                            border-bottom: 1px black solid;

                            &:hover{
                                color: black;
                                background-color:rgba(0, 0, 0, 0);
                                transform: scale(1.1); 
                            }
                        }        
                    }

                    button{
                        padding: 3px 10px;
                        margin-top: 20px;
                        display: block;
                    }
                }

            }

            .menu.menuOpen{
