<template>
    <div class="Carousel-Wrapper">
        <h1>title</h1>
        <div class="swiper enlarge-cl">
            <div class="swiper-wrapper">
                <div class="swiper-slide">
                    <div class="mask"></div>
                    <img src="https://picsum.photos/id/950/700/450" alt="">
                </div>
                <div class="swiper-slide">
                    <div class="mask"></div>
                    <img src="https://picsum.photos/id/411/700/450" alt="">
                </div>
                <div class="swiper-slide">
                    <div class="mask"></div>
                    <img src="https://picsum.photos/id/317/700/450" alt="">
                </div>
                <div class="swiper-slide">
                    <div class="mask"></div>
                    <img src="https://picsum.photos/id/318/700/450" alt="">
                </div>
            </div>
            <div class="swiper-pagination"></div>
        </div>
    </div>
</template>

<script>
import Swiper, { Navigation, Pagination } from 'swiper';

export default {
    name:'enlarge-cl',
    mounted() {
        const swiper2 = new Swiper('.enlarge-cl', {
        modules: [Navigation, Pagination],
        direction: 'horizontal',
        loop: true,
        effect:'slide',
        grabCursor:true,
        slidesPerView:2,
        centeredSlides:true,
        
        pagination: {
            el:'.swiper-pagination',
            bulletClass:'swiper-pagination-bullet',
            bulletActiveClass:'swiper-pagination-bullet-active',
            clickable:true,            
        },
    
        });
    },

}
</script>

<style lang="less" scoped>
    .Carousel-Wrapper{
        max-width: 1920px;
        margin: 50px auto 0 auto;
        padding: 30px 0;
        background-image: url('https://picsum.photos/id/888/1920/583');
        background-position: center;
        
        h1{
            font-size: 48px;
            text-align: center;
            margin-bottom: 10px;
        }
        .enlarge-cl{
            max-width: 1200px;
            width: 80vw;

            .swiper-slide{
                position: relative;
            }

            .swiper-slide img{
                width: 100%;
                vertical-align: middle;
                transition: transform 1s;
                transform: scale(0.85);
            }

            .swiper-slide-active{
                img{
                    transform: scale(1.15);
                } 
            }

            .swiper-slide-duplicate-active{
                img{
                    transform: scale(1.15);
                }
            }

            .swiper-slide {
                .mask{
                    position: absolute;
                    bottom: 0;
                    top: 0;
                    left: 0;
                    right: 0;
                    background-color: rgba(0, 0, 0, 0.5);
                    z-index: 999;
                    transform: scale(0.85);
                    transition: transform 1s;
                }
            }
            .swiper-slide-active{
                .mask{
                    transform: scale(1.15);
                    opacity: 0;
                }

            } 
            .swiper-slide-duplicate-active{
                .mask{
                    transform: scale(1.15);
                    opacity: 0;
                }   
            } 
        }
    }
</style>

<style lang="less" scoped>
    // 768
    @media (max-width:768px) {
        .Carousel-Wrapper{
            background-image: url('https://picsum.photos/id/888/1000/330');
            
            h1{
                font-size: 40px;
                text-align: center;
                margin-bottom: 20px;
            }
        }
    }
</style>

<style lang="less">
     .enlarge-cl{
        .swiper-pagination-bullet{
            border-radius: 0;
            width: 50px;
