            opacity: 1;
            background-color: rgb(255, 255, 255);
        }
        .swiper-pagination-bullet-active{
            background-color:rgb(111, 160, 235);
        }
     } 
</style>

<style lang="less">
    // 768
    @media (max-width:768px) {
        .Carousel-Wrapper{
            .enlarge-cl{
                .swiper-pagination-bullet{
                    width: 25px;
